'use server';

import { revalidatePath } from 'next/cache';
import { z } from 'zod';
import { addMessage } from './data';

const FormSchema = z.object({
  message: z
    .string()
    .min(1, 'Message cannot be empty.')
    .max(500, 'Message is too long.'),
  recipient: z
    .string()
    .min(1, 'Recipient cannot be empty.')
    .max(50, 'Recipient name is too long.'),
});

export type FormState = {
  message?: string;
  errors?: {
    recipient?: string[];
    message?: string[];
  };
  success: boolean;
  recipient?: string;
};

export async function sendMessage(
  prevState: FormState,
  formData: FormData
): Promise<FormState> {
  const validatedFields = FormSchema.safeParse({
    recipient: formData.get('recipient'),
    message: formData.get('message'),
  });

  if (!validatedFields.success) {
    return {
      success: false,
      errors: validatedFields.error.flatten().fieldErrors,
    };
  }

  const { recipient, message } = validatedFields.data;

  try {
    // Now calls the async version that writes to Firestore
    await addMessage(message, recipient);
    revalidatePath(`/bottle/${recipient}`);
    return { success: true, recipient };
  } catch (e: any) {
    return {
      success: false,
      message: e.message || 'An unexpected error occurred.',
    };
  }
}
