'use server';

import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  query,
  where,
  getDoc,
  doc,
  serverTimestamp,
} from 'firebase-admin/firestore';
import { initializeFirebase } from '@/firebase';
import { revalidatePath } from 'next/cache';
import { v4 as uuidv4 } from 'uuid';

// This file will be updated to use the Admin SDK if server-side operations are needed.
// For now, we will assume client-side fetching and remove direct initialization here.
// The pages/components will be responsible for getting the firestore instance.

export type Message = {
  id: string;
  content: string;
  recipient: string;
  timestamp: any; // Firestore timestamp
  senderId?: string; // Optional sender ID for anonymous messages
  isPolished?: boolean;
};

export type Recipient = {
  name: string;
  messageCount: number;
};

// This function is called from a Server Action, so it needs its own firestore instance.
export async function addMessage(
  content: string,
  recipient: string
): Promise<Message> {
  const { firestore } = initializeFirebase(); // This should be a server-side admin instance
  const messageId = uuidv4();
  const newMessage: Omit<Message, 'timestamp'> = {
    id: messageId,
    content,
    recipient: recipient.toLowerCase().trim(),
    // We can add a senderId if we have authenticated users
  };

  // The 'public_messages' collection is now being written to from the server.
  await getFirestore()
    .collection('public_messages')
    .doc(messageId)
    .set({
      ...newMessage,
      timestamp: serverTimestamp(),
    });

  revalidatePath('/browse');
  revalidatePath(`/bottle/${recipient}`);

  // Optimistically return the message shape
  return {
    ...newMessage,
    timestamp: new Date(), // Return a local date for immediate UI feedback
  };
}

// These functions are called from Server Components, they need their own firestore instance.
export async function getMessagesForRecipient(
  recipient: string
): Promise<Message[]> {
  const { firestore } = initializeFirebase();
  const q = query(
    collection(getFirestore(), 'public_messages'),
    where('recipient', '==', recipient.toLowerCase().trim())
  );
  const querySnapshot = await getDocs(q);
  const messages: Message[] = [];
  querySnapshot.forEach((doc) => {
    const data = doc.data();
    messages.push({
      id: doc.id,
      content: data.content,
      recipient: data.recipient,
      timestamp: data.timestamp?.toDate(), // Convert Firestore Timestamp to Date
    });
  });
  return messages.sort((a, b) => b.timestamp - a.timestamp);
}

export async function getMessageById(id: string): Promise<Message | undefined> {
  const { firestore } = initializeFirebase();
  const docRef = doc(getFirestore(), 'public_messages', id);
  const docSnap = await getDoc(docRef);

  if (docSnap.exists()) {
    const data = docSnap.data();
    return {
      id: docSnap.id,
      content: data.content,
      recipient: data.recipient,
      timestamp: data.timestamp?.toDate(),
    };
  } else {
    return undefined;
  }
}

export async function getRecipients(): Promise<Recipient[]> {
  const { firestore } = initializeFirebase();
  const querySnapshot = await getDocs(
    collection(getFirestore(), 'public_messages')
  );
  const recipientMap = new Map<string, number>();

  querySnapshot.forEach((doc) => {
    const recipient = doc.data().recipient;
    if (recipient) {
      recipientMap.set(recipient, (recipientMap.get(recipient) || 0) + 1);
    }
  });

  return Array.from(recipientMap.entries()).map(([name, messageCount]) => ({
    name,
    messageCount,
  }));
}
